
.container{
display: flex;
flex-wrap:wrap;
  border-collapse: collapse;
  margin: 0;
  width: 100%;
  object-fit: contain;
  box-sizing: border-box;
  position: relative;
}



.diag{
  width: 100%;
  height: 100%;
  background:linear-gradient(to bottom right, transparent calc(50% - 1px), black calc(50% - 1px), black 50%, transparent 50%),
              linear-gradient(to bottom left, transparent calc(50% - 1px), black calc(50% - 1px), black 50%, transparent 50%);

}

.diagBlock{
  justify-content: center;
  display: flex;
  align-items: center;
  object-fit: contain;
  box-sizing: border-box;
  position: relative;
  justify-content: center;
  display: flex;
  object-fit: contain;
  box-sizing: border-box;
  position: relative;
  border:0.25px solid #000000cc;
  width: 50%;
  height: 1000px;
}

.infoBlock{
  width: 50%;
  height: 1000px;
  justify-content: center;
  display: flex;
  align-items: center;
  object-fit: contain;
  box-sizing: border-box;
  position: relative;
  justify-content: center;
  display: flex;
  object-fit: contain;
  box-sizing: border-box;
  position: relative;
  border:0.25px solid #000000cc;
}

.contentBlock{
  height: 95%;
  width: 95%;
}

.contentUl{
  width: 100%;
  list-style: none;
  display: inline-block;
}

.contentLi{
  width: 100%;
}

.input{
  width: 80%;
  border: 0;
  border-bottom: 1px solid black;
  padding: 0;
  margin: 0;

}