*{
    margin:0;
    padding: 0;

}

.image{
    width: 90%;
    height: 90%;
}


.Header{
    font-size: 2fw;
    width: 100%;
    height: 11vw;
    display: flex;
    flex-wrap:wrap;

}

.container{
    height: 80%;
    width: 80%;

}
.Cart{
    position: static;;
    font-size: 1vw;
    float:right;
}
.ul{
    
    list-style: none;
}

.blocktitle{
    width: 25%;
    height: 100%;
        object-fit: contain;
        box-sizing: border-box;
        position: relative;
        border:0.25px solid #000000cc;
}
.block{
  
    width: 25%;
    height: auto;
        object-fit: contain;
        box-sizing: border-box;
        position: relative;

}
.block2{

    width: 50%;
    height: auto;
        object-fit: contain;
        box-sizing: border-box;
        position: relative;

}





.halfblockcontent, .halfblockcontentBag, .halfblockcontentTitleTag {

    align-items: center;
    display: flex;
    width: 80%;
    height: 80%;
    font-size: 1.7vw;
   
}
.halfblockcontentBag{
    height: 100%;
    font-size: 1.6vw;
    width: 80%;

}

.bag{

    width: 50%;
    text-align: left;
}
.amount{
    width: 50%;
    font-style: italic;
text-align: right;
}

.amountTag{
   font-style: normal;
}

.halfblock1{

    justify-content: center;
    display: flex;
    align-items: center;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
    display: flex;
    height: 50%;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    border:0.25px solid #000000cc;

}
.halfblock2{
    justify-content: center;
    display: flex;
    align-items: center;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
    display: flex;
    height: 50%;
    object-fit: contain;

    position: relative;
    border-left:0.25px solid #000000cc;


}

.halfblocklist{
    justify-content: center;
    display: flex;
    align-items: center;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
    display: flex;
    width: 100%;
    height: 50%;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    border:0.25px solid #000000cc;


}
.halfblockdiag{
    justify-content: center;
    display: flex;
    align-items: center;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    justify-content: center;
    display: flex;
    width: 100%;
    height: 50%;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    border:0.25px solid #000000cc;

}

.diag{
    width: 100%;
    height: 100%;
    background:linear-gradient(to bottom right, transparent calc(50% - 1px), black calc(50% - 1px), black 50%, transparent 50%),
                linear-gradient(to bottom left, transparent calc(50% - 1px), black calc(50% - 1px), black 50%, transparent 50%);

}


.title{
    width: 100%;
    height: 100%;
    position: static;;
    display: flex;
    justify-content: center;
    align-items: center;
font-size: 3vw;
}

.pagetitle{
 

}
.amount{
    right: 0;
}
.pageslist, .pageslist2{
padding-left: 13vw;
gap: 13vw;
display: flex;
list-style: none;
}
.pageslist{
    padding-left: 10vw;
    gap: 10vw;
}


 @media (max-width:1200px){

    .halfblockdiag{
        width: 0; 
        height: 0;
    }
    .halfblocklist{
        width: 150%;
        border-right: 0;
    }
    .pageslist{
        padding-left: 50vw;
        gap: 10vw;
       
    }

    .halfblockcontent{
     
        color: grey;
        width: 95%;
    }

    .halfblockcontentBag{
    
        font-size: 1.4vw;
    
    }

    .pageslist{
        padding-left: 10vw;
        gap: 10vw;
    }
    .Header{
        height: 16vw;
    }
    .pageslist{
        font-size: 2.5vw;
    }
    .halfblockcontentBag{
        font-size: 1.6vw;
    
    }
 
 }


 @media (min-width:1540px){

    .pageslist{
        padding-left: 6vw;
        gap: 6vw;
        font-size: 1vw;
    }
    .halfblockcontent{
        font-size: 1vw;
    }
    .halfblockcontentBag{
        font-size: 0.8vw;
    }
    .Header{
        height: 8vw;
    }
 }
 @media (max-width:1540px){
    .pageslist{
        font-size: 1.3vw;
    }

    .pageslist{
        padding-left: 6vw;
        gap: 6vw;
        font-size: 1.3vw;
    }
    .halfblockcontentBag{
        font-size: 1vw;
    
    }
    .halfblockcontent{
        
        font-size: 1.3vw;
    }
 }


 @media (max-width:1200px){
    .pageslist{
        padding-left: 8vw; // was 8
        gap: 8vw;          // was 8 
    }
    .Header{
        height: 20vw;
    }
    .pageslist{
        font-size: 3.5vw;
    }
    .halfblockcontentBag{
        font-size: 2.9vw;
        
    
    }
    .halfblockcontent{
        
        font-size: 3vw;
    }
 }


 @media (max-width:450px){

    .Header{
        height: 20vw;
    }
    .pageslist{
        padding-left: 7vw; // was 8
        gap: 7vw;          // was 8 
    }
 }


 
