.container{
    width: 50%;
    height: auto;
    background-color: #ecececcc;
    object-fit: contain;
    box-sizing: border-box;
    position: relative;
    border:0.25px solid #000000cc;
    overflow: hidden;;
}


@keyframes skeleton-loading {
    0% {
      background-position: -200px 0;
    }
    100% {
      background-position: calc(200px + 100%) 0;
    }
  }
  
  .container {
    display: inline-block;
    background: linear-gradient(90deg, #f0f0f0 50%, #e0e0e0 50%, #f0f0f0 100%);
    background-size: 200px 100%;
    animation: skeleton-loading 4s infinite linear;
  }
  
  .skeletonText {

  }
  
  .skeletonImage {


  }



.price{
    font-style: italic;
}
.mainBlock{
    
 
    width: 80%;
    display: flex;
}

.block{
   align-items: end;
    top: 0px;
    right: 0px;
    position:absolute;
 
    width: 20%;
    height: 100%;
    display: flex;

}

.textBlock{

text-align:left;
font-size: 1.3vw;
position:absolute; 
bottom:20px;
left:5%; 
width: 90%;

}
.price{

    position:absolute; 
   color:black;
   right: 0;
 
}
*{
    margin:0;
}
.imageUrl{
    height: 73.5vw;
    overflow: hidden;;
    display: flex;
    position: relative;
    width: 100%;
}
@media (min-width:1200px){
    .container{
        width: 25%;
        height: 37.2vw;
    }
    .textBlock{
        font-size: 1vw;
    }


}
